:focus, :active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

li {
  list-style: none;
}

html {
  font-size: 16px;
  font-family: "Roboto Condensed", sans-serif;
  line-height: 150%;
  font-weight: 400;
  font-style: normal;
  color: #000000;
}

.cf:after,
.cf:before {
  content: " ";
  display: table;
}

.cf:after {
  clear: both;
}

.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
}
@media (max-width: 1182px) {
  .container {
    max-width: 970px;
  }
}
@media screen and (max-width: 992.98px) {
  .container {
    max-width: 720px;
  }
}
@media screen and (max-width: 767.98px) {
  .container {
    max-width: none;
    padding: 0 10px;
  }
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;
}

a {
  color: inherit;
  cursor: pointer;
}

img {
  max-width: 100%;
}

.vhidden {
  position: absolute;
  height: 1px;
  width: 1px;
  margin: -1px;
  border: nonr;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

h1 {
  color: red;
}

.burger {
  display: none;
}
@media screen and (max-width: 992.98px) {
  .burger {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    z-index: 3;
  }
}
@media screen and (max-width: 992.98px) {
  .burger:before, .burger:after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    background-color: #ffffff;
    height: 4px;
    transition: all 0.3s ease 0s;
  }
}
@media screen and (max-width: 992.98px) {
  .burger:before {
    top: 0;
  }
}
@media screen and (max-width: 992.98px) {
  .burger:after {
    bottom: 0;
  }
}
@media screen and (max-width: 992.98px) {
  .burger span {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    background-color: #ffffff;
    height: 4px;
    top: 8px;
    transition: all 0.3s ease 0s;
  }
}

@media screen and (max-width: 992.98px) {
  .burger .active:before {
    transform: rotate(45deg);
    top: 8px;
  }
}

@media screen and (max-width: 992.98px) {
  .burger .active:after {
    transform: rotate(-45deg);
    bottom: 8px;
  }
}

@media screen and (max-width: 992.98px) {
  .burger .active span {
    transform: scale(0);
  }
}

@media screen and (max-width: 992.98px) {
  .nav {
    position: absolute;
    overflow: auto;
    top: -200%;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000000;
    z-index: 2;
    transition: all 0.5s ease 0s;
  }
}
.nav__list {
  display: flex;
}
@media screen and (max-width: 992.98px) {
  .nav__list {
    flex-direction: column;
    margin: 100px 30px;
  }
}
@media screen and (max-width: 992.98px) {
  .nav__item {
    display: block;
    border-bottom: 1px solid #ffffff;
    padding: 20px 0;
  }
}
@media screen and (max-width: 992.98px) {
  .nav__link {
    display: block;
  }
}

@media screen and (max-width: 992.98px) {
  .nav.active {
    top: 0;
  }
}